import { Component, OnInit, HostListener } from '@angular/core';
import { NavigationService } from '../common/navigation.service';
import * as $ from 'node_modules/jquery/dist/jquery';

@Component({
  selector: 'app-home-page',
  templateUrl: './home-page.component.html',
  styleUrls: ['./home-page.component.css']
})
export class HomePageComponent implements OnInit {

  constructor(public nvtService: NavigationService) { }

  ngOnInit() {
    $('#homePageHidden').css('height', $('#navBarMainPage').height() + 'px');
    $(window).scroll(function () {
      $('.btn-navbar-custom').addClass('slide-in-top', $(this).scrollTop() > ($('#menuPage').after().offset().top - $('#navBarMainPage').height()));
    });
  }

  @HostListener('window:resize', ['$event'])
  onResize(event) {
    this.nvtService.homeIdNavigator(this.nvtService.getMyGV());
  }

  homeIdNavigator(navigatedId) {
    this.nvtService.homeIdNavigator(navigatedId);
  }

}
