import { Component, OnInit, OnDestroy } from '@angular/core';
import { Router } from '@angular/router';
import { ViewportScroller } from "@angular/common";
import * as $ from 'node_modules/jquery/dist/jquery';
import { NavigationService } from 'src/app/common/navigation.service';

@Component({
  selector: 'appHomePageMain',
  templateUrl: './hp-home.component.html',
  styleUrls: ['./hp-home.component.css']
})
export class HPHomeComponent implements OnInit, OnDestroy {

  public scrollControl: Number;
  constructor(
    private router: Router,
    private viewportScroller: ViewportScroller,
    public nvtService: NavigationService) { }

  ngOnInit() {
    // Add an event listener to window
    // Window can be defined in the pollyfiles.ts as: 
    // if (window) {
    //    (window as any).global = window;
    // }
    this.scrollControl = 0;
    window.addEventListener('scroll', this.scroll, true); //third parameter
  }

  ngOnDestroy() {
    window.removeEventListener('scroll', this.scroll, true);
  }

  scroll = (event: any): void => {
    this.scrollControl = $(document).scrollTop();
  };

  homeIdNavigator(navigatedId) {
    this.nvtService.homeIdNavigator(navigatedId);
  }

}
