import { Injectable } from '@angular/core';
import * as $ from 'node_modules/jquery/dist/jquery';

@Injectable({
  providedIn: 'root'
})
export class NavigationService {
  public lastUsedScreenId: any;

  constructor() {
    this.lastUsedScreenId = 'homePageHidden';
  }

  setMyGV(val) {
    this.lastUsedScreenId = val;
  }

  getMyGV() {
    return this.lastUsedScreenId;
  }

  homeIdNavigator(navigatedId) {
    const scrollMove = ($('#' + navigatedId).after().offset().top) - $('#navBarMainPage').height();
    $(document).scrollTop(scrollMove);

    this.setMyGV(navigatedId);
  }
}
