import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomePageComponent } from './home-page/home-page.component';
import { ServicesComponent } from './home-page/hp-menu/services/services.component';
import { HomeComponent } from './home-page/hp-menu/home/home.component';
import { ConsultingComponent } from './home-page/hp-menu/consulting/consulting.component';
import { ProjectsComponent } from './home-page/hp-menu/projects/projects.component';


const routes: Routes = [
  {path: '', pathMatch: 'full', redirectTo: 'home'},
  {path: 'home', component: HomePageComponent, children: [
    // {path: '', pathMatch: 'full', redirectTo: 'main'},
    {path: 'main', component: HomeComponent},
    {path: 'services', component: ServicesComponent},
    {path: 'projects', component: ProjectsComponent},
    {path: 'consulting', component: ConsultingComponent},
  ]}
];

@NgModule({
  imports: [
    RouterModule.forRoot(routes, {
      scrollPositionRestoration: 'disabled',
      anchorScrolling: 'enabled',
    })
  ],
  exports: [RouterModule]
})
export class AppRoutingModule { }
