import { Component, OnInit } from '@angular/core';
import * as jQuery from 'node_modules/jquery/dist/jquery';
import { NavigationService } from 'src/app/common/navigation.service';
import * as $ from 'node_modules/jquery/dist/jquery';

@Component({
  selector: 'appHomePageMenu',
  templateUrl: './hp-menu.component.html',
  styleUrls: ['./hp-menu.component.css']
})
export class HPMenuComponent implements OnInit {

  constructor(public nvtService: NavigationService) { }

  ngOnInit() {
    (function($) {

      "use strict";
    
      var fullHeight = function() {
    
        $('.js-fullheight').css('height', $(window).height());
        $(window).resize(function(){
          $('.js-fullheight').css('height', $(window).height());
        });
    
      };
      fullHeight();
    
      $('#sidebarCollapse').on('click', function () {
          $('#sidebar').toggleClass('active');
      });
    
    })(jQuery);
  }

}
